import React from "react"
import LottieBackground from "../../components/LottieBackground";
import animationData from './data-comp-v1.json';
import QuickHeader from "../../components/QuickHeader"


const BG = ({ bg, color, source, height }: any) => (
    <div
      style={{
        borderRadius: 20,
        width: 'calc(100% - 20px)',
        marginLeft : 10,
        marginTop: 10,
        backgroundColor: 'red', // Background color of the container
      }}
    >
      <div
        style={{
          backgroundColor: bg ?? '#FEAE37', // Can be used for the background
          width: '100%',
          height: height ?? 180,
          overflow: 'hidden',
        }}
      >
        <LottieBackground
          speed={0.25}
          zoom={1}
          animationData={source}
          color={ color ?? 'rgba(0,0,0,0.2)'} // Pass dynamic color to change the animation's color
          //style={{ opacity: 0.1 }}
        />
      </div>
    </div>
  );

  


export default() =>{
    return(<>
        <QuickHeader/>
        <div style={{ maxWidth : 1090 }}>
        {/* Example with dynamically changing colors */}
        <BG bg="#FEAE37" height="50vh" source={animationData} /> 
  
</div>
    </>)
}